.footer {
    background-color: white;
    &-nav {
        background-color: #bd001a;
        padding: 20px 0px;
        @include m() {
            padding: 0px 0px 20px; } }
    &-ul {
        display: flex;
        justify-content: space-between;
        @include m() {
            display: block; }
        a {
            display: block;
            width: 186px;
            line-height: 6rem;
            text-align: center;
            position: relative;
            z-index: 0;
            &::after {
                @include psuedoDefault(0px);
                background-color: $colorDefault;
                z-index: -1;
                transform: skewX(-18deg); }
            @include m() {
                line-height: 5.5rem; } } }
    &-li {
        color: white;
        &:first-of-type {
            display: flex;
            @include m() {
                flex-wrap: wrap; }
            a {
                &:not(:last-of-type) {
                    margin-right: 1px; }
                &:first-of-type {
                    width: 100px;
                    text-align: left;
                    @include m() {
                        width: 100%;
                        margin-right: 0px;
                        text-align: center; }
                    &::after {
                        display: none; } } } }
        &:last-of-type {
            transform: translateX(-10px);
            @include m() {
                transform: translateX(0px); }
            a {
                &::after {
                    background-color:#a40016 {}
                    @include m() {
                        transform: skewX(18deg); } } } }
        .text-nihon {
            @extend %_f16Mincho;
            @include m() {
                width: 100%; } }
        .text-en {
            @extend %_f20R;
            @include m() {
                width: calc(50% - 0.5px); } } }
    &-link {
        max-width: 584px;
        padding: 0px 20px;
        width: 100%;
        margin: 80px auto;
        text-align: center;
        @include m() {
            margin: 40px auto; } }
    &-logo {
        display: table;
        margin: 0px auto 40px;
        @include m() {
            margin: 0px auto 25px; } }
    &-wrap {
        display: flex;
        justify-content: space-between; }
    &-list {
        max-width: 174px;
        width: 100%;
        @include m() {
            max-width: 157px; }
        a {
            display: table;
            line-height: 1.6rem;
            border-bottom: 1px solid #333333; }
        @include m() {
            &:first-of-type {
                min-width: 157px;
                margin-right: 10px; } } }
    .copyright {
        color: white;
        font-size: 1.4rem;
        line-height: 6rem;
        text-align: center;
        background-color: #333333;
        padding: 0px 20px; } }
.BackTop {
    max-width: calc(50% - 633px);
    width: 100%;
    min-width: 20px;
    position: fixed;
    z-index: 9;
    height: 0px;
    bottom: 100px;
    right: 0px;
    @include m() {
        bottom: 60px; }
    #Backtop {
        position: absolute;
        left: 0px;
        bottom: 0px;
        width: 72px;
        height: 72px;
        transform: translateX(-100%);
        background-color: $colorDefault;
        @include m() {
            max-width: 60px;
            min-width: 45px;
            width: 12.5vw;
            height: 12.5vw;
            max-height: 60px;
            min-height: 45px; } } }

