@import "../Abstract/__variables";
@import "../Abstract/__extend";
@import "../Abstract/__mixin";

@charset "UTF-8";

@font-face {
    font-family: "yumin";
    src: url(../assets/fonts/yumin.ttf);
    font-weight: 400; }

* {
    box-sizing: border-box;
    padding: 0px;
    margin: 0px; }
html {
    font-size: 10px; }
body {
    font-size: 1.6rem;
    font-family: "游ゴシック Medium", "Yu Gothic Medium", "游ゴシック体",
    "YuGothic", "ヒラギノ角ゴ Pro W3", "Hiragino Kaku Gothic ProN",
    "Hiragino Kaku Gothic Pro", "メイリオ", "ＭＳ ゴシック", sans-serif {}
    color: #333333;
    overflow-x: hidden;
    @include m() {
        font-size: 1.4rem; }
    &.active {
        transform: scale(0.9); } }
a {
    color: inherit;
    text-decoration: none; }
button, a {
    outline: none; }
.container {
    max-width: 1120px;
    width: 100%;
    margin: auto;
    @include t() {
        padding: 0 20px; } }
body::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    border-radius: 20px;
    background-color: #f5f5f5; }

body::-webkit-scrollbar {
    width: 3px;
    background-color: #f5f5f5; }

body::-webkit-scrollbar-thumb {
    border-radius: 20px;
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    background-color: #555; }
h1, h2, h3, h4, h5, h6 {
    font-weight: bold; }

@include CSSIE() {
    body, main {
        overflow-x: hidden; } }
li {
    list-style-type: none; }

.ef-gradient {
    position: relative;
    display: block;
    opacity: 0;
    &::after, &::before {
        @include psuedoDefault(0px);
        transform: skewX(-15deg);
        @include m() {
            transform: skewX(0deg); } }
    &::after {
        z-index: -1;
        // animation-name: gradientEf
        animation-fill-mode: backwards;
        animation-duration: 1s;
        animation-timing-function: ease;
        transition: all 0.3s ease;
        @include gradient($colorDefault); }
    &::before {
        opacity: 0;
        z-index: 1;
        // animation-name: opacityEf
        animation-duration: 1.5s;
        animation-timing-function: ease;
        animation-fill-mode: forwards;
        animation-delay: 1s;
        @include gradient2($colorDefault); }
    &>* {
        // animation-name: opacity
        opacity: 0;
        animation-duration: 0.01s;
        animation-fill-mode: forwards;
        animation-delay: 1.1s; }
    &:hover {
        &::after {
            background-position: 75% 50%; } }
    &.active {
        opacity: 1;
        &>* {
            animation-name: opacity; }
        &::after {
            animation-name: gradientEf; }
        &::before {
            animation-name: opacityEf; } } }
@import "../Layout/__header";
@import "../Layout/__footer";
@import "../Components/__button";
@import "../Components/__libraryCustom";
@import "../Abstract/__function";
