.header {
    display: flex;
    justify-content: space-between;
    position: fixed;
    top: 0px;
    left: 0px;
    width: 100%;
    z-index: 10;
    padding-left: 76px;
    align-items: center;
    height: 80px;
    transition: height 0.3s ease;
    &::before {
        @include psuedoDefault(0px);
        z-index: -2;
        background-color: rgba(black, 0.6);
        opacity: 0;
        transition: all 0.3s ease; }
    &.active {
        height: 65px;
        &::before {
            opacity: 1; } }
    &.hidden {
        &::before {
            transform: translateY(-100%); } }
    @include t() {
        padding-left: 20px; }
    @include m() {
        height: 15.625vw !important;
        min-height: 50px;
        max-height: 60px; }
    &-logo {
        @include CSSIE() {
            width: 100%; }
        a {
            display: flex;
            align-items: center;
            img {
                display: block;
                margin-right: 12px;
                @include m() {
                    height: 12.5vw;
                    min-height: 40px;
                    max-height: 50px;
                    margin-right: 6px; } } } }
    &-sologan {
        @extend %_f20R;
        color: $colorDefault; }
    &-nav, &-menu, &-list {
        height: 100%; }
    &-menu {
        display: flex;
        justify-content: flex-end;
        transform: translateX(-12px);
        @include m() {
            transform: translateX(0px); } }
    &-list {
        color: white;
        width: 140px;
        position: relative;
        z-index: 0;
        display: flex;
        cursor: pointer;
        &:not(.bar) {
            margin-right: 1px;
            &::after {
                @include gradient($colorDefault); }
            &::before {
                @include gradient2($colorDefault); }
            @include m() {
                display: none; } }
        & > a {
            display: flex;
            height: 100%;
            width: 100%;
            justify-content: center;
            flex-direction: column;
            align-items: center; }
        span {
            display: block;
            line-height: 1; }
        .text-nihon {
            @extend %_f12Mincho;
            margin-bottom: 2px; }
        .text-en {
            @extend %_f20R; }
        .text-special {
            @extend %_f16Mincho; }
        &.bar {
            width: 80px;
            align-items: center;
            justify-content: center;
            @include m() {
                width: 15.625vw;
                min-width: 50px;
                max-width: 60px; }
            &::after {
                @include gradient(white); }
            &::before {
                @include gradient2(white); } }
        &.active {
            &::after {
                @include gradient(#a40016); }
            &::before {
                @include gradient2(#a40016); } }
        &::after, &::before {
            @include psuedoDefault(0px);
            transform: skewX(-15deg);
            @include m() {
                transform: skewX(0deg); } }
        &::after {
            z-index: -1;
            animation-name: gradientEf;
            animation-fill-mode: backwards;
            animation-duration: 1s;
            animation-timing-function: ease;
            transition: all 0.3s ease; }
        &::before {
            opacity: 0;
            z-index: 1;
            animation-name: opacityEf;
            animation-duration: 1.5s;
            animation-timing-function: ease;
            animation-fill-mode: forwards;
            animation-delay: 1s; }
        &>* {
            animation-name: opacity;
            opacity: 0;
            animation-duration: 0.01s;
            animation-fill-mode: forwards;
            animation-delay: 1.1s; }
        &:hover {
            &::after {
                background-position: 75% 50%; } } }
    .nav-menu {
        position: fixed;
        z-index: -1;
        top: 0px;
        left: 0px;
        background-color: rgba(black, 0.9);
        width: 100%;
        height: 100vh;
        padding-top: 80px;
        transform: translateX(-100%);
        color: white;
        letter-spacing: 0.1rem;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        transition: transform 0.5s ease;
        overflow-y: auto;
        &.active {
            transform: translateX(0%); }
        .text-nihon {
            font-family: "yumin", sans-serif; }
        .text-en {
            font-family: "Roboto Condensed", sans-serif;
            font-weight: 600; }
        @include m() {
            padding: 0px 20px 0px;
            justify-content: flex-start;
            // position: relative
            &::before {
                content: '';
                min-height: 70px;
                max-height: 160px;
                height: 21.875vw; } }
        .nav-list {
            max-width: 400px;
            width: 100%;
            // margin: 0px auto
            a {
                width: 100%; }
            &:not(:last-of-type) {
                margin-bottom: 10px;
                span {
                    display: block;
                    width: 100%;
                    line-height: 5rem;
                    text-align: center; }
                a {
                    position: absolute;
                    top: 0px;
                    left: 0px;
                    z-index: 1;
                    height: 100%; } }
            &.menu-button {
                display: none;
                a {
                    line-height: 5rem;
                    text-align: center;
                    width: 100%;
                    &:first-of-type {
                        margin-right: 1px; }
                    &:not(:last-of-type) {
                        margin-bottom: 10px;
                        width: calc(50% - 0.5px); } }
                @include m() {
                    display: flex;
                    flex-wrap: wrap; } } } } }



