@import "../Abstract/__mixin";
@import "../Abstract/__variables";

.btn {
    opacity: 1;
    transition: opacity 0.3s ease;
    &:hover {
        opacity: 0.8; } }
.bar-nav {
    display: flex;
    height: 26px;
    width: 30px;
    position: relative;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    span {
        position: absolute;
        width: 100%;
        right: 0px;
        height: 2px;
        background-color: #333333;
        transform-origin: center center;
        transition: all 0.3s ease;
        &:first-of-type {
            transform: translateY(-12px);
            @include m() {
                transform: translateY(-13px); } }
        &:last-of-type {
            transform: translateY(12px);
            @include m() {
                transform: translateY(13px); } } } }
.bar {
    &.cross-henshin {
        .bar-nav {
            span {
                transform: rotate(45deg);
                &:last-of-type {
                    transform: rotate(135deg);
                    width: 100%; } } } } }
.arrows {
    display: flex;
    padding: 25px 10px 0px;
    @include m() {
        padding: 0px 7px;
        align-items: flex-end; }
    span {
        width: 50%;
        height: 100%;
        overflow: hidden;
        position: relative;
        z-index: 0;
        @include m() {
            height: 65%; }
        &::after {
            content: '';
            position: absolute;
            z-index: 1;
            background-color: white;
            height: 2px;
            width: 100%;
            transition: transform 0.3s ease;
            top: 0px; }
        &:first-of-type {
            &::after {
                right: 0px;
                transform-origin: top right;
                transform: rotate(-45deg); } }
        &:last-of-type {
            &::after {
                left: 0px;
                transform-origin: top left;
                transform: rotate(45deg); } } }
    &:hover {
        opacity: 0.8 !important;
        span {
            &:first-of-type {
                &::after {
                    transform: rotate(-55deg); } }
            &:last-of-type {
                &::after {
                    transform: rotate(55deg); } } } } }
#Backtop {
    visibility: hidden;
    opacity: 0;
    transition: all 0.3s ease;
    &.active {
        visibility: visible;
        opacity: 1; } }
.arrow-hover {
    &:hover {
        .arrow {
            transform: translateX(5px);
            &::before {
                transform: rotate(-50deg); } } } }
.icon-tabNew {
    position: relative;
    z-index: 0;
    &::after, &::before {
        content: '';
        position: absolute;
        z-index: 1;
        height: 9px;
        width: 9px; }
    &::after {
        border-left: 1px solid #333333;
        border-bottom: 1px solid #333333;
        bottom: 3px;
        right: 15px; }
    &::before {
        right: 11px;
        bottom: 6px;
        border: 1px solid #333333; } }
