@keyframes gradientEf {
    0% {
        background-position: 0% 50%; }
    100% {
        background-position: 100% 50%; } }
@keyframes opacityEf {
    0% {
        opacity: 0;
 }        // background-position: 80% 50%
    1% {
        opacity: 1;
        background-position: 100% 50%; }
    100% {
        background-position: 0 50%;
        opacity: 1; } }
@keyframes opacity {
    0% {
        opacity: 0; }
    100% {
        opacity: 1; } }
